
import { defineComponent } from 'vue';
import {
  IonRow,
  IonCol,
  IonLabel,
  IonTitle,
  IonButtons,
  IonButton,
  IonIcon,
} from '@ionic/vue';
import { mapMutations } from 'vuex';
import Popover from '@/components/ui/Popover.vue';

export default defineComponent({
  name: 'Specification',
  components: {
    Popover,
    IonRow,
    IonCol,
    IonLabel,
    IonTitle,
    IonButtons,
    IonButton,
    IonIcon,
  },
  props: {
    manufacturer: {
      type: String,
      default: '',
    },
    type: {
      type: String,
      default: '',
    },
    brand: {
      type: String,
      default: '',
    },
    period: {
      type: String,
      default: '',
    },
    material: {
      type: String,
      default: '',
    },
  },
  methods: {
    ...mapMutations(['SET_POPOVER']),
    openPopover() {
      this.SET_POPOVER({
        show: true,
        message: [this.$t('REPLACEMENT-PERIOD-MESSAGE')],
      });
    },
    closePopover() {
      this.SET_POPOVER({
        show: false,
        message: [''],
      });
    },
  },
});
